import React from 'react';
import { getComputedStyleX } from './getComputedStyle';
import { IRegion, IOption, IOffset, ICssPropertyName } from '../interface';
import { getOffset, setOffset, getScrollLeft, getScrollTop } from './position-utils';

// typescript 1.5版本从document上移除了parentWindow属性;
// https://stackoverflow.com/questions/38973426/using-documentmode-with-typescript
export interface OurDocument extends Document {
  parentWindow?: Window;
}

type UtilType = {
  getWindow: (node: any) => Window;
  getDocument: (node: HTMLElement) => OurDocument;
  isWindow: (obj: any) => obj is Window;
  isDocument: (ele: any) => ele is OurDocument;

  getWindowScrollLeft: (w: Window) => number;
  getWindowScrollTop: (w: Window) => number;

  // 传offset，则设置元素相对文档的左上距离；否则获取元素相对文档的左上距离
  offset: (el: HTMLElement, offset?: IOffset, option?: IOption) => IRegion | undefined;
  css: (el: HTMLElement, name: ICssPropertyName | React.CSSProperties, v?: number | string) => any;

  clone: (obj: object) => object;
  mix: (to: Object, from: Object) => Object;
  each: (arr: any[], fn: Function) => void;
  merge: (...args: any[]) => any;
};

const utils: UtilType = {
  getWindow: (node: any): Window => {
    if (node && node.document && node.setTimeout) {
      return node;
    }
    const doc = node.ownerDocument || node;
    return doc.defaultView || doc.parentWindow;
  },
  getDocument: (node: HTMLElement): OurDocument => {
    if (utils.isWindow(node)) {
      return node.document;
    }
    if (utils.isDocument(node)) {
      return node;
    }
    return node.ownerDocument;
  },
  /**
   * A crude way of determining if an object is a window
   * @member util
   */
  isWindow: (obj: any): obj is Window => {
    // must use == for ie8
    /* eslint eqeqeq:0 */
    return obj !== null && obj !== undefined && obj == obj.window;
  },
  isDocument: (ele: any): ele is OurDocument => {
    return ele.nodeType === 9;
  },
  getWindowScrollLeft: (w: Window) => {
    return getScrollLeft(w);
  },
  getWindowScrollTop: (w: Window) => {
    return getScrollTop(w);
  },
  offset: (el: HTMLElement, offset?: IOffset, option?: IOption): IRegion | undefined => {
    if (typeof offset !== 'undefined') {
      setOffset(el, offset, option || {});
    } else {
      return getOffset(el);
    }
  },
  css: (el: HTMLElement, name: ICssPropertyName | React.CSSProperties, v?: number | string): any => {
    let value: number | string | undefined = v;
    if (typeof name === 'object') {
      for (const i in name) {
        if (Object.prototype.hasOwnProperty.call(name, i)) {
          utils.css(el, i as ICssPropertyName, name[i as ICssPropertyName]);
        }
      }
      return undefined;
    }
    if (typeof value !== 'undefined') {
      if (typeof value === 'number') {
        value = `${value}px`;
      }
      el.style[name as any] = value;
      return undefined;
    }
    return getComputedStyleX(el, name);
  },
  clone: (obj: React.CSSProperties): React.CSSProperties => {
    let ret: React.CSSProperties = {};
    // 老的写法
    // for (const i in obj) {
    //   if (obj.hasOwnProperty(i)) {
    //     ret[i] = obj[i as keyof Object];
    //   }
    // }
    Object.assign(ret, obj);
    const overflow = obj.overflow;
    if (overflow) {
      // 老的写法
      // for (const i in obj) {
      //   if (obj.hasOwnProperty(i)) {
      //     (ret.overflow!!)[i] = overflow[i];
      //   }
      // }
      Object.keys(obj).forEach((i) => {
        (ret.overflow!! as any)[i] = (overflow as any)[i];
      });
    }
    return ret;
  },
  mix: (to: Object, from: Object) => {
    // 老的写法
    // for (const i in from) {
    //   if (from.hasOwnProperty(i)) {
    //     to[i] = from[i as keyof Object];
    //   }
    // }
    Object.assign(to, from);
    return to;
  },
  each: (arr: any[], fn: Function) => {
    for (let i = 0; i < arr.length; i++) {
      fn(arr[i]);
    }
  },
  merge: (...args: any[]): any => {
    const ret = {};
    for (let i = 0; i < args.length; i++) {
      utils.mix(ret, args[i]);
    }
    return ret;
  },
};


export { utils };